$logo-big: 120px;
$logo: 40px;
$big-padding: 100px;
$collapse: 700px;
$sticky: true;

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  box-shadow: var(--shadow);

  @if $sticky {
    position: sticky !important;
    top: 0;
    z-index: 10 !important;
  }
}

header a {
  color: #1f2b72;
  text-decoration: none;
  font-weight: 700;
}

.home {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.logo {
  height: $logo;
}

.logo > * {
  width: unset;
  height: 100%;
}

.title-text {
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  flex-wrap: wrap;
  gap: 5px;
  min-width: 0;
  font-family: var(--title);
  text-align: left;
}

.title {
  font-size: var(--large);
}

.subtitle {
  opacity: 0.65;
  font-weight: var(--thin);
}

.nav-toggle {
  display: none;
  position: relative;
  width: 30px;
  height: 30px;
  margin: 0;
  color: var(--text);
  appearance: none;
  transition: background var(--transition);
}

.nav-toggle:after {
  content: "\f0c9";
  position: absolute;
  left: 50%;
  top: 50%;
  color: var(--text);
  font-size: 15px;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  transform: translate(-50%, -50%);
}

.nav-toggle:checked:after {
  content: "\f00d";
}

nav {
  display: flex;
  justify-content: center;
  font-size: 30px;
  align-items: center;
  flex-wrap: wrap;
  gap: 100px;
  font-family: var(--heading);
  line-height: 1.3;
  margin-top: 30px;
  margin-bottom: 0;
}

nav > a {
  padding: 5px;
}

nav > a:hover {
  color: var(--primary);
}

header:not([data-big]) {
  @media (max-width: $collapse) {
    justify-content: flex-end;

    .nav-toggle {
      display: flex;
    }

    .nav-toggle:not(:checked) + nav {
      display: none;
    }

    nav {
      align-items: flex-end;
      flex-direction: column;
      width: 100%;
    }
  }
}

header[data-big] {
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px 20px 20px;

  @if $sticky {
    top: unset;
  }

  .home {
    flex-direction: column;
    flex-grow: 0;
  }

  .logo {
    height: $logo-big;
    flex-shrink: 0;
  }

  .title-text {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .title {
    font-size: 3.2rem;        
    font-weight: 700;
    line-height: 1.2;
    letter-spacing: -0.02em;  
    color: #000000;
  }

  .subtitle {
    font-size: 3.0rem;        
    font-weight: 700;
    line-height: 1.2;
    letter-spacing: -0.02em;  
    color: #003A8F;
  }
}

header:not([data-big]) .title-text {
  align-items: center;
}

header:not([data-big]) .title {
  line-height: 2.0;
}

header:not([data-big]) .subtitle {
  line-height: 1.0;
}

/* ===== NAV ITEM ===== */
.nav-item {
  position: relative;
}

/* ===== DROPDOWN PANEL ===== */
.dropdown {
  position: absolute;
  top: calc(100% + 10px);          /* 14px → 10px */
  left: 50%;
  transform: translateX(-50%) translateY(6px); /* 8px → 6px */
  min-width: 180px;                /* 260px → 200px */
  padding: 8px 0;                  /* 12px → 8px */
  border-radius: 12px;             /* 14px → 12px */
  box-shadow: 0 16px 32px rgba(0, 0, 0, 0.12); /* 살짝 축소 */
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s ease;
  z-index: 999;
  background: rgba(255, 255, 255, 0.521);  /* 반투명 */
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);   /* Safari 대응 */
}

/* 작은 화살표 */
.dropdown::before {
  content: "";
  position: absolute;
  top: -7px;                       /* 8px → 7px */
  left: 50%;
  transform: translateX(-50%);
  border-width: 0 7px 7px 7px;
  border-style: solid;
  border-color: transparent transparent #ffffff transparent;
}


/* hover 시 표시 */
.nav-item:hover .dropdown {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%) translateY(0);
}

/* ===== DROPDOWN LINKS ===== */
.dropdown a {
  position: relative;              /* underline 애니메이션용 */
  display: block;
  padding: 8px 15px;               /* 12px 24px → 축소 */
  font-size: 0.85rem;              /* 1.05rem → 0.95rem */
  font-weight: 500;
  color: #1f2b72;
  text-align: center;
  text-decoration: none;
  transition: color 0.2s ease;
}

.dropdown a::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 4px;
  width: 35px;                    /* 글씨 길이에 비례 */
  height: 1.8px;                   /* 더 얇게 */
  background-color: #707072;
  transform: translateX(-50%) scaleX(0);
  transform-origin: left;
  transition: transform 0.25s cubic-bezier(.4,0,.2,1);
}

/* hover 효과 */
.dropdown a:hover {
  color: #213ddf;
  background: none;                /* 기존 배경 제거 */
}

.dropdown a:hover::after {
  transform: translateX(-50%) scaleX(1);
}

/* ===== PARENT NAV LINK ===== */
.nav-item > a {
  display: inline-block;
  padding: 12px 22px;   /* 24 → 22 */
  border-radius: 10px;
  font-size: 1.25rem;   /* 1.4 → 1.25 */
  font-weight: 500;
  color: #1f2b72;
  text-decoration: none;
  transition: all 0.2s ease;
}


/* 부모 hover 효과 */
.nav-item:hover > a {
  background: rgba(31, 43, 114, 0.08);
  color: #213ddf;
}

/* ===== MESSAGE NAV ===== */
.message-nav {
  display: flex;
  gap: 16px;
  margin: 32px 0;
}

/* ===== MESSAGE NAV LINKS ===== */
.message-nav a {
  display: inline-block;
  padding: 12px 24px;
  border-radius: 14px;
  font-size: 1.05rem;
  font-weight: 500;
  color: #1f2b72;
  background: #ffffff;
  text-decoration: none;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.25s ease;
}

/* hover 효과 */
.message-nav a:hover {
  background: rgba(31, 43, 114, 0.08);
  color: #213ddf;
  transform: translateY(-2px);
}